import Autocomplete from "@mui/material/Autocomplete"
import TextField from "@mui/material/TextField"
import * as React from "react"
import { forwardRef, } from "react"
import { PAPER_SIZES, PaperSizeFromDimensions } from "./common"















export const PaperAutocomplete = forwardRef(
  (
    {
      disableClearance = false,
      onChange,
      value,
      label,
    },
    ref,
  ) => {
    let paperSize = null
    if (value) {
      const [a, b] = value
      paperSize = PaperSizeFromDimensions(a, b)
    }
    return (
      React.createElement(Autocomplete, {
        ref: ref,
        disableClearable: disableClearance,
        size: "small",
        freeSolo: true,
        renderInput: (params) => (
          React.createElement(TextField, { ...params, label: label, variant: "outlined",} )
        ),
        value: paperSize,
        onChange: (e, newValue) => {
          if ("string" === typeof newValue) {
            if (newValue.trim() === "") {
              if (!disableClearance) {
                onChange(null)
              }
            } else {
              const match = /(\d+(?:\.\d+)?).*?(\d+(?:\.\d+)?)/.exec(newValue)
              if (match) {
                const [, widthStr, heightStr] = match
                onChange(PaperSizeFromDimensions(+widthStr, +heightStr))
              }
            }
          } else {
            onChange(newValue)
          }
        },
        getOptionLabel: (val) => {
          const [width, height, name = "custom"] = val
          return `${name} ${width}x${height}mm`
        },
        options: PAPER_SIZES,}
      )
    )
  },
)
