import Button from "@mui/material/Button"
import fileDownload from "js-file-download"

import * as React from "react"
import ReactDOMServer from "react-dom/server"
import { SvgPrintContext } from "./Measure"

export function ExportButtons({
  what,
  baseFileName,
}


) {
  const getPrintSVG = () =>
    ReactDOMServer.renderToStaticMarkup(
      React.createElement(SvgPrintContext.Provider, { value: true,}, what),
    ).replace(/\s{2,}/g, " ")
  const asSVG = () => {
    const svg = getPrintSVG()
    fileDownload(svg, baseFileName + ".svg")
  }
  const asTemplatePDF = async () => {
    const { svgToPdf } = await import(
      /* webpackChunkName: "svgToPdf" */ "../paperBox1/svgToPdf"
    )

    // add your content to the document here, as usual
    const blob = await svgToPdf({
      title: baseFileName,
      author: "Adrian Leonhard",
      svg: getPrintSVG(),
    })

    fileDownload(blob, baseFileName + ".pdf")
  }
  return (
    React.createElement(React.Fragment, null
      , React.createElement(Button, { variant: "contained", onClick: asSVG,}, "Download As SVG"

      )
      , React.createElement(Button, { variant: "contained", onClick: asTemplatePDF,}, "Template as PDF"

      )
    )
  )
}
