import { SVGPathData } from "svg-pathdata"

export const M = (x, y) => ({
  type: SVGPathData.MOVE_TO,
  x,
  y,
  relative: false,
})
export const m = (x, y) => ({
  type: SVGPathData.MOVE_TO,
  x,
  y,
  relative: true,
})
export const L = (x, y) => ({
  type: SVGPathData.LINE_TO,
  x,
  y,
  relative: false,
})
export const l = (x, y) => ({
  type: SVGPathData.LINE_TO,
  x,
  y,
  relative: true,
})

export const Z = () => ({ type: SVGPathData.CLOSE_PATH })
