import debounce from "lodash/debounce"
import mapValues from "lodash/mapValues"
import { useCallback, useEffect, useRef, useState } from "react"

const parseHash = (hash) => {
  return !hash
    ? {}
    : hash
        .substring(1)
        .split("&")
        .map((part) => {
          const [key, value] = part.split("=")
          return [decodeURIComponent(key), decodeURIComponent(value)]
        })
        .reduce((obj, [key, value]) => {
          obj[key] = value
          return obj
        }, {} )
}
const objectToHash = (o) => {
  return (
    "#" +
    Object.entries(o)
      .map(
        ([key, value]) =>
          encodeURIComponent(key) + "=" + encodeURIComponent(value),
      )
      .join("&")
  )
}

const defaultDeserialize = (
  x,
) =>
  mapValues(x, (v) => {
    if ("true" === v) {
      return true
    } else if ("false" === v) {
      return false
    } else if ("NaN" === v) {
      return NaN
    } else if ("undefined" === v) {
      return undefined
    } else if ("null" === v) {
      return null
    } else if ("" === v) {
      return ""
    } else if (!isNaN(+v)) {
      return +v
    } else {
      return v
    }
  })
const defaultSerialize = (x) =>
  mapValues(x, (v) => "" + v)

export function useHashState(
  initialState,
  {
    deserialize = defaultDeserialize,
    serialize = defaultSerialize,
    wait = 1000,
  } = {},
) {
  const createInitialState = useCallback(() => {
    const state =
      "function" === typeof initialState
        ? (initialState )()
        : initialState
    // hash overrides parameter
    const mergedState = deserialize(
      Object.assign(serialize(state), parseHash(document.location.hash)),
    ) 
    history.pushState(
      undefined,
      document.title,
      objectToHash(serialize(mergedState)),
    )

    return mergedState
  }, [deserialize, initialState, serialize])
  const [state, setState] = useState(createInitialState)
  const updateHashRef = useRef()
  if (!updateHashRef.current) {
    updateHashRef.current = debounce(function (newState) {
      history.pushState(
        undefined,
        document.title,
        objectToHash(serialize(newState)),
      )
    }, wait)
  }
  useEffect(() => updateHashRef.current(state), [state])

  useEffect(() => {
    const onHashChange = () => {
      setState(createInitialState())
    }
    window.addEventListener("hashchange", onHashChange)
    return () => {
      window.removeEventListener("hashchange", onHashChange)
    }
  }, [createInitialState])
  return [state, setState]
}
